.fanevent {
  height: 100%;
  width: 100%;
  overflow: clip;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.fanevent .flipcard {
  height: 70%;
  width: 100%;
}

.fanevent button {
  height: 3vw;
  width: 13vw;
  border-radius: 12px;
  border: none;
  margin-top: 2vw;
  background: #ffff91;
  box-shadow: 0.1vw 0.1vw 0.2vw #0099e0;
  font-size: small;
}

.faneventframe {
  height: 80%
}

.sectionfanevents grid-item {
  width: 100%;
  height: 100%;
}

.faneventflipcard {
  border-width: 0.5vw;
  width: 94%;
}

.faneventflipcard .faneventback {
  background: #0099e000;
}


label:hover .faneventflipcard {
  transform: rotateX(2deg);
  box-shadow: 10px -10px 5px rgba(0, 0, 0, 0);
}